@use '../../../library';

.MuiPaper-root {
  &.callAction--home {
    background-color: library.$orange;
    min-width: 75%;
    display: flex;
    align-self: center;
    padding: 4rem;
    margin: 4rem;
    border-radius: 15px;
  }
}

.MuiButtonBase-root {
  &.callAction--buttonFirst {
    // margin-right: 1rem;
    padding-right: 1rem;
    margin: 1rem 1rem 1rem 0;
    background-color: library.$coral;
  }
  
  &.callAction--buttonSecond {
    color: library.$white;
    border: 1px solid library.$white;
  }
}

.callAction--buttonDiv {
  padding-top: 1.5rem;
  // justify-content: space-between;
}

.MuiTypography-root {
  &.callAction--text {
    color: library.$white;
    text-decoration: none; 
    font-size: 32px; 
    font-weight: 800;
  }
}

.containCTA {
  max-width: 400px;
  min-width: 300px;
  right: 0;
  top: 30%;
  height: auto;
  position: relative;
  align-items: center;
  justify-content: center;
  margin-bottom: -100px;
  margin-top: -150px;
}